<template>
  <div class="vueWrap">
    <el-table
      :span-method="objectSpanMethod"
      style="width: 800px"
      :data="tableBody"
      border
      :header-cell-style="{
        background: '#FAFAFA',
        color: '#333333',
        fontWeight: 'bold',
        fontSize: '14px',
      }"
    >
      <el-table-column
        type="index"
        label=""
        width="58"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="toolsKinds"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="toolsName"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="price"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="remark"
        label=""
        align="center"
      >
        <template slot-scope="scope">
          <el-button
            type="text"
            @click="delBtn(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "tableRow",
  data() {
    return {
      // 
      tableBody: [
        {
          toolsKinds: "",
          toolsName: "",
          price: 10,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 20,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 30,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 88,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 44,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 999,
          remark: "",
        },
        {
          toolsKinds: "",
          toolsName: "",
          price: 1001,
          remark: "",
        },
      ],
      cellList: [], // 
      count: null, // 
    };
  },
  mounted() {
    // 1
    this.computeCell(this.tableBody);
  },
  methods: {
    delBtn(row) {
      console.log(row);
    },
    computeCell(tableBody) {
      // 
      for (let i = 0; i < tableBody.length; i++) {
        if (i == 0) {
          // 
          this.cellList.push(1); // 1cellList0
          this.count = 0; // 0
          console.log("", 0, this.count);
        } else {
          // 
          if (tableBody[i].toolsKinds == tableBody[i - 1].toolsKinds) {
            // 
            this.cellList[this.count] += 1; // 
            this.cellList.push(0); // cellList0
            console.log("", i, this.count);
          } else {
            this.cellList.push(1); // cellList1
            this.count = i; // 
            console.log("", i, this.count);
          }
        }
      }
    },
    // 2el-table
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      // 01
      if (columnIndex === 1) {
        console.log("0", this.cellList);
        const rowCell = this.cellList[rowIndex];
        if (rowCell > 0) {
          const colCell = 1;
          console.log(
            `, ${colCell}${rowCell} `
          );
          return {
            rowspan: rowCell,
            colspan: colCell,
          };
        } else {
          // 
          // 
          return {
            rowspan: 0,
            colspan: 0,
          };
        }
      }
    },
  },
};
</script>