<template>
  <div class="index">
    <!--  -->
    <el-select v-model="addressValue" multiple filterable remote reserve-keyword placeholder=""
      :remote-method="getsuggest" :loading="loading" style="width: 100%;margin-bottom: 30px;">
      <el-option v-for="item in addressList" :key="item.id" :label="item.title + '' + item.address" :value="item.id">
      </el-option>
    </el-select>
    <div id="container"></div>
  </div>
</template>
<script>
import API from "@/api/admin";
export default {
  data() {
    return {
      addressValue: "",
      addressList: [],
      //

      markerArr: [
        {
          id: "1", //id

          styleId: "myStyle", //id

          position: new TMap.LatLng(30.33610725981676, 112.24231780622101), //

          properties: {
            //

            title: "",
          },
        },
      ],
    };
  },
  mounted() {
    // 
    this.initMap();
  },
  created() {
    // API.login({ user: "666" }).then((res) => {});
  },
  methods: {
    //
    /**
     * @param {} e
     */
    getsuggest: function (e) {
      let that = this;
      if (e.length <= 0) {
        //data
        that.addressList = [];
        return;
      }
      // 
      this.$jsonp("https://apis.map.qq.com/ws/place/v1/suggestion/", {
        region: "",

        keyword: e,

        key: "RBNBZ-QUO6F-4NWJN-NYP2R-U22Z2-QFBTP",

        output: "jsonp"

      })
        .then((res) => {
          //
          console.log(that.city);
          console.log(res);
          that.addressList = res.data;
        })
        .catch((err) => {
          console.log(err);
          this.$message.error("");
        });
    },
    initMap() {
      // ()

      var center = new TMap.LatLng(30.35269, 112.19016);

      //

      var map = new TMap.Map(document.getElementById("container"), {
        center: center, //

        // zoom: 17.2,   //

        // pitch: 43.5,  //

        // rotation: 45    //
      });

      map.setCenter(new TMap.LatLng(this.lat, this.lng));

      //

      // IP

      var ipLocation = new TMap.service.IPLocation();

      // ipip(params)

      var params = {};

      ipLocation
        .locate(params) //ip

        .then((result2) => {
          console.log(result2.result.location);

          //ip

          map.setCenter(
            new TMap.LatLng(
              result2.result.location.lat,
              result2.result.location.lng
            )
          );
        })

        .catch((error) => {
          console.log(error);
        });

      //marker

      var markerLayer = new TMap.MultiMarker({
        map: map, //

        //

        styles: {
          //styleId"myStyle"stylesstyleId

          myStyle: new TMap.MarkerStyle({
            width: 25, // 

            height: 35, // 

            // "src": './images/R-C.png',  //

            //

            // "anchor": { x: 16, y: 32 }  //marker
          }),
        },

        //

        geometries: this.markerArr,

        // 

        //   geometries: [

        //     {

        //     "id": "1",   //id

        //     "styleId": 'myStyle',  //id

        //     "position": new TMap.LatLng(30.33864600881732, 112.22565830821229),  //

        //     "properties": {//

        //       "title": "",

        //     }

        //   }

        // ]
      });

      //

      var infoWindow = new TMap.InfoWindow({
        map: map,

        position: new TMap.LatLng(39.984104, 116.307503), //
      });

      // 

      infoWindow.close();

      //marker

      markerLayer.on("click", function (evt) {
        infoWindow.close(); //

        infoWindow.open(); // 

        infoWindow.setPosition(evt.geometry.position); //

        infoWindow.setContent(
          `<div><img class="img" src="https://t7.baidu.com/it/u=4198287529,2774471735&fm=193&f=GIF" alt=""><p>${evt.geometry.properties.title}</p></div>`
        ); //

        console.log(evt);
      });

      // 

      // map.on('click',function(evt){

      // console.log(evt);

      // })
    },
  },
};
</script>
<style scoped>
#container {
  width: 100%;
  height: 100%;
  border: 1px solid red;
}

.img {
  width: 150px;
}
</style>