<template>
    <div>
        <el-form :model="form" :inline="true">
            <el-form-item label="ID">
                <el-input v-model="form.id"></el-input></el-form-item>
            <el-form-item label="">
                <el-input v-model="form.username"></el-input></el-form-item>
            <el-form-item label="">
                <el-input v-model="form.phone"></el-input></el-form-item>
            <el-form-item label="">
                <el-input v-model="form.password"></el-input></el-form-item>
        </el-form>
        <el-button @click="submit" type="primary"></el-button>
        <el-button @click="edit" type="waring"></el-button>
        <el-table :data="tableData" border tooltip-effect="dark" style="width: 100%"
            :header-cell-style="{ 'text-align': 'center' }" :cell-style="{ 'text-align': 'center' }">
            <el-table-column prop="id" label="ID"></el-table-column>
            <el-table-column prop="username" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="password" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="text" @click="del(scope.row)"></el-button>
                </template>
            </el-table-column>

        </el-table>
    </div>
</template>
<script>
import API from "@/api/admin.js";

export default {
    name: "myDatabase",
    data() {
        return {
            tableData: [],
            form: {
                id: '',
                username: '',
                phone: "",
                password: ''
            }
        };
    },
    created() {
        this.userInfo();
    },
    methods: {
        userInfo() {
            let t = this;
            API.userInfo().then((res) => {
                t.tableData = res.data
            });
        },
        submit() {
            let idList = []
            this.tableData.map(item => {
                idList.push(item.id.toString())
            })
            if (idList.indexOf(this.form.id) > -1) {
                this.$message.error('ID')
                return
            }
            if (!this.form.id) {
                this.$message.error('ID')
                return
            }
            API.addUser(this.form).then(res => {
                if (res.data.code == 200) {
                    this.$message.success('')
                    this.userInfo()
                }
            })
        },
        edit() {
            let idList = []
            this.tableData.map(item => {
                idList.push(item.id.toString())
            })
            if (idList.indexOf(this.form.id) == -1) {
                this.$message.error('')
                return
            }
            if (!this.form.id) {
                this.$message.error('')
                return
            }
            API.updateUser(this.form).then(res => {
                if (res.data.code == 200) {
                    this.$message.success('')
                    this.userInfo()
                }
            })
        },
        del(row) {
            this.$confirm("" + row.username)
                .then(() => {
                    API.delUser({ id: row.id }).then(res => {
                        if (res.data.code == 200) {
                            this.$message.success('')
                            this.userInfo()
                        }
                    })
                })
                .catch(() => { });
        }
    },
};
</script>