<template>
  <div class="white-body-view">
    <el-button type="primary" @click="addNewRecord()"></el-button>
    <custom-tree ref="customTree" :tree-data="treeData" :tree-expand-all="treeExpandAll" :tree-node-key="treeNodeKey" @addItem="addTreeItem" @deleteItem="deleteTreeItem" @editItem="editTreeItem" />
    <!--  -->
    <place-dialog ref="placeDialog" @addData="addData" @editData="editData" />
  </div>
</template>
<script>
import CustomTree from '@/components/tree'
import PlaceDialog from '@/components/tree-dialog'
export default {
  components: {
    CustomTree,
    PlaceDialog
  },

  data() {
    return {
      treeData: [],
      treeExpandAll: true,
      treeNodeKey: 'id'
    }
  },

  created() {
    this.initTreeData()
  },

  methods: {
    // 
    initTreeData() {
      this.treeData = [
        {
          children: [
            {
              children: [],
              name: '1',
              desc: '1',
              parentId: '1',
              id: '2'
            },
            {
              children: [],
              name: '2',
              desc: '1',
              parentId: '1',
              id: '3'
            },
            {
              children: [],
              name: '3',
              desc: '3',
              parentId: '1',
              id: '4'
            }
          ],
          name: '',
          parentId: '',
          id: '1'
        },
        {
          children: [
            {
              children: [],
              name: '1',
              desc: '',
              parentId: '5',
              id: '6'
            }
          ],
          name: '',
          parentId: '',
          id: '5'
        }
      ]
    },

    // 
    addNewRecord() {
      this.$refs.placeDialog.openDialog(false)
    },

    // 
    addData(data) {
      // 
      this.$refs.customTree.treeAddItem(data)
    },

    // 
    editData(data) {
      // 
      this.$refs.customTree.treeEditItem(data)
    },

    // 
    addTreeItem(data) {
      // 
      this.$refs.placeDialog.openDialog(false, data.id)
    },

    // 
    deleteTreeItem(data) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        this.$refs.customTree.treeDeleteItem(data)
        // 
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },

    // 
    editTreeItem(data) {
      // 
      this.$refs.placeDialog.openDialog(true, data.parentId, data)
    }

  }
}
</script>
